.navbar {
  position: fixed;
  top: 0;
  width: 100%;
  height: 30px;
  padding: 20px;
  display: flex;
  justify-content: space-around;
  background-color: #111;
}

.logo {
  position: fixed;
  left: 20px;
  width: 80px;
  cursor: pointer;
}

.avatar {
  position: fixed;
  right: 20px;
  width: 30px;
  cursor: pointer;
}
